.btn {
  display: inline-block;
  padding: 0.6rem 1rem;
  font-size: 1.5rem;
  border: 1px solid transparent;
  white-space: nowrap;
  cursor: pointer;
  border-radius: 4px;

  &:focus {
    outline: none;
  }

  &:disabled {
    opacity: 0.65;
    cursor: not-allowed;
  }

  &-block {
    display: block;
  }

  &-icon {
    display: inline-block;
    vertical-align: middle;
    fill: $color-primary;
    width: 2.5rem;
    margin-right: 0.3rem;
  }

  &-primary {
    background-color: $color-primary;
    color: #fff;
  }

  &-outline-white {
    border: 2px solid #fff;
    background-color: transparent;
    color: #fff;

    &:hover {
      transition: all 0.3s ease;
      background-color: #fff;
      color: $color-primary;
    }
  }

  &-outline-primary {
    border: 1px solid $color-primary;
    background-color: #fff;
    color: $color-primary;
  }

  &-light {
    background-color: #fff;
    color: $color-primary;
  }

  &-radius {
    border-radius: 5rem;
  }

  &-lg {
    padding: 1.2rem 2.5rem;
    font-size: 1.8rem;
  }

  &-md {
    padding: 1rem 1.8rem;
    font-size: 1.7rem;
  }
}

.loading-icon {
  display: inline-block;
  width: 1.5rem;
  height: 1.5rem;
  color: #fff;
  animation: spin 1s infinite;
  transition: all 0.3s;
  transition-timing-function: ease-in;
  position: relative;

  &::after {
    position: absolute;
    top: 0;
    left: 0;
    content: "";
    display: inline-block;
    border-color: currentColor currentColor currentColor transparent;
    border-width: 2px;
    border-style: solid;
    border-radius: 50%;
    width: 100%;
    height: 100%;
  }
}

@keyframes spin {
  0% {
    transform: rotate(0);
    animation-timing-function: cubic-bezier(0.55, 0.055, 0.675, 0.19);
  }

  50% {
    transform: rotate(180deg);
    animation-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
  }

  100% {
    transform: rotate(360deg);
  }
}
