.modal {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 9999;
  background-color: rgb(0, 0, 0, 0.5);
  width: 100%;
  height: 100%;
}

.modal .modal-content {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 50%;
  background-color: #fff;
  padding: 2rem;
  border-radius: 4px;
}

.modal .modal-body {
  max-height: 100%;
  overflow-y: auto;
  overflow-x: hidden;
}
@media only screen and (max-width: 780px) {
  .modal .modal-content {
    width: 90%;
  }
}
